
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}
#nav {
  padding: 30px;
  text-align: center;
}
#nav a {
  font-weight: bold;
  color: #2c3e50;
}
#nav a:hover {
  color: grey;
  border-radius: .5rem;
}
#nav a.router-link-exact-active {
  color: whitesmoke;
  background: orangered;
  border-radius: .5rem;
}
@media (max-width: 575px) {
#row {
        height: 50px !important;
}
}

img {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  margin: 5px;
  max-width: 75%;
  height: auto;
}

